.spinner {
  display: none;
  width: 64px;
	height: 64px;
	clear: both;
	margin: 20px auto;
	border: 4px rgba(255, 255, 255, 0.25) solid;
	border-top: 4px rgba(255, 255, 255, 1) solid;
	border-radius: 50%;
	-webkit-animation: rotation 1s infinite linear;
	animation: rotation 1s infinite linear;
}
